




























import App from '../../../../App.vue'
import Api from '../../../../api.js'
export default {
	data() {
		return {
			url:'',
			errorImage: 'this.src=' + this.url,
			showed1: false,
			showed2: false,
			identity: "",
			avatarUrl: '',
			name: '',
			shopName: '',
			shops: []
		};
	},
	methods: {
		bindPickerChange: function(e) {
			var that = this
			console.log(e)
			console.log('picker发送选择改变，携带值为', e.target.value)
			var ind = e.target.value
			that.shopName = that.shops[e.target.value].company_name
			App.savecompany(that.shops[e.target.value])
		},
		jump(url) {
			if (!url) return;
			uni.navigateTo({
				url
			});
		},
		toQRCode: function() {
			var that = this
			if (App.getToken()) {
				uni.navigateTo({
					url: 'QRCodeCard/QRCodeCard'
				})
			} else {
				uni.showToast({
					title: '未登录',
					icon: 'none'
				})
			}
		}
	},
	onShow() {
		var that = this;
		if (App.getToken()) {
			console.log(App.getcompany())
			if (App.getcompany()) {
				that.showed1 = false
				that.showed2 = true
				this.shopName = App.getcompany().company_name;
				this.identity = '店员'
				uni.request({
					url: Api.shop(),
					header: {
						token: App.getToken()
					},
					success: function(res) {
						if (res.data.code == 200) {
							that.shops = res.data.value
						} else {
							uni.showToast({
								title: '获取门店失败',
								duration: 2000,
								icon: 'none'
							})
						}
					},
				})
			} else {
				uni.request({
					url: Api.shop(),
					header: {
						token: App.getToken()
					},
					success: function(res) {
						if (res.data.code == 200) {
							that.showed1 = false
							that.showed2 = true
							that.identity = '店员'
							that.shopName = '请选择门店'
							console.log(res)
							that.shops = res.data.value;
							console.log(that.shops);
						} else if (res.data.code == 1005) {
							that.shopName = '无'
							that.identity = '游客'
							that.showed1 = true
							that.showed2 = false
							// uni.showToast({
							// 	title: '您不属于任何商家',
							// 	duration: 2000,
							// 	icon:'none'
							// })
						}
					}
				})
			}
			console.log(App.getcompany().company_name);
			uni.request({
				url: Api.seeUser(),
				header: {
					token: App.getToken(),
				},
				data: {
					otherId: App.getUserId()
				},
				success(res) {
					console.log(res)
					if (res.data.code) {
						uni.showToast({
							title: 'token失效！',
							icon: 'none'
						})
						uni.clearStorage()
					} else {
						that.url = App.geturlerror(res.data.user.author_image)
						console.log(that.url)
						that.avatarUrl = App.geturl(res.data.user.author_image)
						that.name = res.data.user.author_name
					}
				}
			})
		} else {
			uni.showToast({
				title: '未登录',
				icon: 'none'
			})
			setTimeout(function() {
				uni.navigateTo({
					url: '../login'
				})
			}, 1000)
		}
	}
}
